.Editor {
    
    width: 100vw;
    height: 30vh;
    display: flex;
    justify-content: center;
  }
  .input-area {
    position: relative;
    outline: none;
  }

  .edit-window{
    width: 50vw;
    background-color: #282c34;
  }
  
  .preview {
    position: absolute;
    display: inline-block;
    background: transparent;
    padding: 0;
    bottom: 0;
    top: 0;
    width: 100%;
    height: 35vh;
    z-index: 3;
  }
  
  .textarea {
    position: absolute;
    display: inline-block;
    padding: 0;
    width: 100%;
    height: 35vh;
    z-index: 3;
  
    margin: 0;
      padding: 0;
      background: none;
      border: none;
      border-radius: 0;
      outline: none;
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
    opacity:1%
  }
  